import { useState, useEffect } from 'react';
import { useSocket } from '@/context/SocketContext';

export default function ChatBox({ receiverId, receiverType, currentUserId, currentUserType }) {
  const socket = useSocket();
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
console.log("current",currentUserId);
console.log("receriver in chat box id",receiverId)
console.log("receiverId",receiverType);
  useEffect(() => {
    if (!socket) return;

    socket.on('newMessage', (msg) => {
      if (
        (msg.senderId === receiverId && msg.receiverId === currentUserId) ||
        (msg.senderId === currentUserId && msg.receiverId === receiverId)
      ) {
        setMessages((prev) => [...prev, msg]);
      }
    });

    return () => {
      socket.off('newMessage');
    };
  }, [socket, receiverId, currentUserId]);

  const sendMessage = () => {
    if (input.trim() === '') return;
   console.log("in send time",input,currentUserId,currentUserType);
   console.log("receriver in send",receiverId,receiverType)
   console.log("receriver type",receiverType);
    socket.emit('sendMessage', {
      senderId: currentUserId,
      receiverId,
      senderType: currentUserType,
      receiverType,
      message: input.trim(),
    });

    setInput('');
  };

  return (
    <div className="border p-4 rounded shadow">
      <div className="h-64 overflow-y-auto bg-gray-100 p-2 mb-2">
        {messages.map((msg) => (
          <div key={msg._id} className={`mb-1 ${msg.senderId === currentUserId ? 'text-right' : 'text-left'}`}>
            <span className="inline-block bg-white p-1 rounded shadow">{msg.message}</span>
          </div>
        ))}
      </div>
      <div className="flex">
        <input
          className="flex-1 border p-1 rounded-l"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
        />
        <button className="bg-blue-500 text-white px-4 rounded-r" onClick={sendMessage}>
          Send
        </button>
      </div>
    </div>
  );
}
